import React, { useState } from "react";

import ThemeToggle from "../fragments/ThemeToggle";
import resume from "../../assets/pdf/BentsenJackResume.pdf";
import Icon from "../fragments/Icon";
import Timeline from "../fragments/Timeline";
import Footer from "../Footer";

export default function About({ theme, setTheme }) {
  const [page, setPage] = useState(0);
  return (
    <>
      <div id="top" className={`about ${theme}`}>
        <div className="about-theme-toggle">
          <ThemeToggle theme={theme} setTheme={setTheme} />
        </div>
        {theme === "dark" ? (
          <div className="MainDivider">
            <svg
              data-name="Layer 1"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 1200 120"
              preserveAspectRatio="none"
            >
              <path
                d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z"
                opacity=".25"
                class="shape-fill"
              ></path>
              <path
                d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z"
                opacity=".5"
                class="shape-fill"
              ></path>
              <path
                d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z"
                class="shape-fill"
              ></path>
            </svg>
          </div>
        ) : (
          <div className="MainDividerLight">
            <svg
              data-name="Layer 1"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 1200 120"
              preserveAspectRatio="none"
            >
              <path
                d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z"
                opacity=".25"
                class="shape-fill"
              ></path>
              <path
                d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z"
                opacity=".5"
                class="shape-fill"
              ></path>
              <path
                d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z"
                class="shape-fill"
              ></path>
            </svg>
          </div>
        )}
        <div className="about-main">
          <h4>A little more about me...</h4>
          <div className={`about-main-txt ${theme}`}>
            <p>
              My web-dev journey only recently began, after enrolling in the UW
              Coding Bootcamp this past summer, after a longstanding interest in
              computers, programming, and technology in general. My family likes
              to tease me about the fact that my primary motivation to learn to
              read as a kid was only to understand what was going on in the
              Legend of Zelda: Orcarina of Time - but I think it paints a pretty
              fitting picture. My interests have always driven me to learn new
              things, and that drive has pulled me in quite a few directions
              through my professional life.
            </p>
            <p>
              I graduated from the George Washington University in 2018 with a
              major in Organizational Science, a music minor, and immedietly ran
              of to pursue my budding career as a professional musician. While
              music is still a large part of my life, I ultimately decided that
              I would find more fulfillment in the techworld and began taking
              the necessary steps to position myself for success in such a
              diverse and rapidly growing industry. I plan to continue my
              education online, including a focus on C++ and C# with the intent
              to transition into Augmented and Virtual Reality software
              development.
            </p>
            <p>
              I personally believe that Augmented/Mixed Reality is the future of
              human-computer interaction and will have a profound impact on the
              experiences we create. We are so close now a completely uncharted
              frontier and I cannot wait to see where this exciting new
              technoloy takes us.
            </p>
          </div>
        </div>
      </div>
      <div className={`about-timeline ${theme}`}>
        <div className={`about-timeline-main ${theme}`}>
          <div className={`about-timeline-control ${theme}`}>
            <button
              className="about-timeline-control-btn"
              onClick={() => {
                if (page > 1) {
                  console.log(page);
                  let val = page - 1;
                  console.log(val);
                  setPage(val);
                }
              }}
            >
              <Icon icon={"chevron-up"} />
            </button>
            <button
              className="about-timeline-control-btn"
              onClick={() => {
                if (page < 2) {
                  let val = page + 1;
                  setPage(val);
                }
              }}
            >
              <Icon icon={"chevron-down"} />
            </button>
          </div>
          <Timeline theme={theme} page={page} setPage={setPage} />
          {theme === "light" ? (
            <p
              style={{
                position: "absolute",
                top: "95%",
                right: "40%",
                display: "flex",
                justifyContent: "center",
                fontSize: "3rem",
              }}
            >
              <a
                style={{ padding: "1rem", color: "black" }}
                href="https://www.github.com/Atlas238"
              >
                <Icon icon={["fab", "github"]} />
              </a>
              <a
                style={{ padding: "1rem", color: "black" }}
                href="https://www.linkedin.com/in/jack-bentsen"
              >
                <Icon icon={["fab", "linkedin"]} />
              </a>
              <a
                style={{ padding: "1rem", color: "black" }}
                href="https://www.facebook.com/Ahtlus"
              >
                <Icon icon={["fab", "facebook"]} />
              </a>
            </p>
          ) : (
            <p
              style={{
                position: "absolute",
                top: "95%",
                right: "40%",
                display: "flex",
                justifyContent: "center",
                fontSize: "3rem",
              }}
            >
              <a
                style={{ padding: "1rem", color: "white" }}
                href="https://www.github.com/Atlas238"
              >
                <Icon icon={["fab", "github"]} />
              </a>
              <a
                style={{ padding: "1rem", color: "white" }}
                href="https://www.linkedin.com/in/jack-bentsen"
              >
                <Icon icon={["fab", "linkedin"]} />
              </a>
              <a
                style={{ padding: "1rem", color: "white" }}
                href="https://www.facebook.com/Ahtlus"
              >
                <Icon icon={["fab", "facebook"]} />
              </a>
            </p>
          )}
        </div>
      </div>
    </>
  );
}
